.Button {
    align-items: center;
    background: #333;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    font-size: 40px;
    height: 70px;
    width: 70px;
    justify-content: center;
    transition: filter 0.3s ease-in;
  }
  
  .Button:active,
  .Button:focus {
    filter: brightness(120%);
  }
  
  .Button.function {
    color: black;
    background: #a5a5a5;
  }
  
  .Button.operator {
    background: #f1a33c;
  }

  .fadelete{
    align-items: center;
    justify-content: center;
    color: black;

  }
  